import {createMuiTheme} from 'material-ui/styles';
const fontWeightMedium = 500;
export default createMuiTheme({
    status: {
        success: 'green'
    },
    typography: {
        // Use the system font.
        fontFamily: '"Source Sans Pro","Helvetica Neue","Helvetica,Arial","sans-serif"',
        fontWeightMedium,
        body1: {
            fontWeight: fontWeightMedium,
        }
    },
    overrides: {
        MuiButton: {
            root: {
                borderRadius: 3,
                background: 'linear-gradient(45deg, #FFFFFF 30%, #FFFFFF 90%)',
                border: '1px solid #d9d9d9',
                color: 'rgba(0, 0, 0, 0.65)',
                minWidth: '44px',
                minHeight: '30px',
                padding: '0 15px',
            }
        },
        MuiInputAdornment: {
            root: {
                position: 'absolute',
                right: '5px'
            }
        },
        MuiTableRow: {
            root: {
                height: '52px'

            }
        },
        MuiTableCell: {
            root: {
                borderBottom: 'none'
            },
            typeHead:{
                fontSize:'14px'
            },
            typeBody:{
                fontSize:'14px'
            }
        },
        Pagination: {
            button: {
                border: 0
            }
        }
    }
})


