import React, {Component} from 'react';
import {connect} from 'react-redux'
import {Redirect, Switch} from 'react-router-dom'
import {ConnectedRouter} from 'react-router-redux'
import {
    Route
} from 'react-router-dom';
import {Reboot} from 'material-ui';
import {MuiThemeProvider} from 'material-ui/styles';
import theme from './theme'
import HOME from './routers/authority'
import login from './routers/login'
import pageError from './routers/PageError'
import systemList from './routers/authority.systemList'
var cookieStore = require('store')

const checkLogin = (PageComp)=>{
    var isLogin = false
    if(cookieStore.get('userInfo')){
        isLogin = true
    }
    return(
        isLogin ? (
            <PageComp />
        ) : (
            <Redirect to="/authority/login"/>
        )
    )
}
class App extends Component {
    render() {
        return (
            <ConnectedRouter history={this.props.history}>
                <MuiThemeProvider theme={theme}>
                    <Reboot></Reboot>
                    <Switch>
                        <Route exact path="/authority/login" component={login}/>
                        <Route exact path="/authority/pageError" component={pageError}/>
                        <Route exact path="/authority/systemList"render={()=>checkLogin(systemList)}/>
                        <Route  path="/authority/home" render={()=>checkLogin(HOME)}/>
                        <Route exact path="/authority" component={()=><Redirect to="/authority/systemList"/>}/>
                        <Route exact path="/" component={()=><Redirect to="/authority"/>}/>
                    </Switch>
                </MuiThemeProvider>
            </ConnectedRouter>
        );
    }
}
function mapStateToProps(state) {
    return {
        app: state.app
    }
}

export default connect(mapStateToProps)(App);



